import * as XLSX from 'xlsx';
import { Flight } from '../types';
import { parseOZExcel } from './companyParsers/ozParser';
import { parseKEExcel } from './companyParsers/keParser';
import { auth } from '../src/firebase/config';

// Excel      
export const parseExcelFile = (file: File, userCompany?: string, userName?: string, empl?: string): Promise<Flight[]> => {
  return new Promise((resolve, reject) => {
    console.log(' Excel  :', { 
      fileName: file.name, 
      userCompany, 
      userName, 
      empl 
    });
    
    const reader = new FileReader();
    
    reader.onload = (e) => {
      try {
        console.log(' Excel   ');
        const data = new Uint8Array(e.target?.result as ArrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });
        
        //    
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        console.log('  :', { sheetName, sheetNames: workbook.SheetNames });
        
        // JSON 
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 }) as any[][];
        console.log(' JSON  :', { 
          totalRows: jsonData.length,
          firstRow: jsonData[0],
          secondRow: jsonData[1]
        });
        
        //    (: OZ)
        const company = userCompany || 'OZ';
        console.log('   :', company);
        
        let flights: Flight[] = [];
        
        //    
        const user = auth.currentUser;
        
        //   
        switch (company) {
          case 'OZ':
            console.log(' OZ   :', { 
              company, 
              jsonDataLength: jsonData.length,
              firstRow: jsonData[0],
              userId: user?.uid 
            });
            flights = parseOZExcel(jsonData, user?.uid);
            console.log(' OZ  :', { 
              flightsCount: flights.length,
              firstFlight: flights[0]
            });
            break;
          case 'KE':
            const keResult = parseKEExcel(jsonData, userName, empl);
            flights = keResult.flights;
            // KE    flight  monthlyTotalBlock 
            //    
            break;
          case '7C':
            throw new Error('(7C) Excel   . PDF  .');
          default:
            console.warn(`    : ${company}, OZ  `);
            flights = parseOZExcel(jsonData);
        }
        
        resolve(flights);
      } catch (error) {
        console.error('Excel  :', error);
        reject(new Error('Excel   : ' + error));
      }
    };
    
    reader.onerror = () => {
      reject(new Error('  .'));
    };
    
    reader.readAsArrayBuffer(file);
  });
};

