// Rate Limiting 
class RateLimiter {
  private requests: Map<string, number[]> = new Map();
  private readonly windowMs: number;
  private readonly maxRequests: number;

  constructor(windowMs: number = 60000, maxRequests: number = 100) {
    this.windowMs = windowMs;
    this.maxRequests = maxRequests;
  }

  isAllowed(identifier: string): boolean {
    const now = Date.now();
    const requests = this.requests.get(identifier) || [];
    
    //   
    const validRequests = requests.filter(time => now - time < this.windowMs);
    
    if (validRequests.length >= this.maxRequests) {
      return false;
    }
    
    //   
    validRequests.push(now);
    this.requests.set(identifier, validRequests);
    
    return true;
  }

  getRemainingRequests(identifier: string): number {
    const now = Date.now();
    const requests = this.requests.get(identifier) || [];
    const validRequests = requests.filter(time => now - time < this.windowMs);
    
    return Math.max(0, this.maxRequests - validRequests.length);
  }
}

export const rateLimiter = new RateLimiter();

// CSRF  
export const generateCSRFToken = (): string => {
  const array = new Uint8Array(32);
  crypto.getRandomValues(array);
  return Array.from(array, byte => byte.toString(16).padStart(2, '0')).join('');
};

// CSRF  
export const validateCSRFToken = (token: string, storedToken: string): boolean => {
  return token === storedToken && token.length === 64;
};

// XSS  
export const sanitizeHTML = (input: string): string => {
  const div = document.createElement('div');
  div.textContent = input;
  return div.innerHTML;
};

// SQL   ( )
export const sanitizeSQL = (input: string): string => {
  return input
    .replace(/['"\\]/g, '')
    .replace(/--/g, '')
    .replace(/\/\*/g, '')
    .replace(/\*\//g, '')
    .replace(/xp_/gi, '')
    .replace(/sp_/gi, '');
};

//    
export const validateFileUpload = (file: File): { isValid: boolean; error?: string } => {
  //    (10MB)
  const maxSize = 10 * 1024 * 1024;
  if (file.size > maxSize) {
    return { isValid: false, error: '   . ( 10MB)' };
  }

  //   
  const allowedTypes = [
    'application/pdf',
    'application/vnd.ms-excel',
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    'text/csv',
    'image/jpeg',
    'image/png',
    'image/gif'
  ];

  if (!allowedTypes.includes(file.type)) {
    return { isValid: false, error: '   .' };
  }

  //  
  const fileName = file.name;
  if (fileName.length > 255) {
    return { isValid: false, error: '  .' };
  }

  //    
  const dangerousPatterns = [
    /\.\./,  //  
    /[<>:"|?*]/,  // 
    /^(CON|PRN|AUX|NUL|COM[1-9]|LPT[1-9])$/i  // Windows 
  ];

  for (const pattern of dangerousPatterns) {
    if (pattern.test(fileName)) {
      return { isValid: false, error: '  .' };
    }
  }

  return { isValid: true };
};

//  IP  (  )
export const getClientIP = (request: any): string => {
  return (
    request.headers['x-forwarded-for']?.split(',')[0] ||
    request.headers['x-real-ip'] ||
    request.connection?.remoteAddress ||
    request.socket?.remoteAddress ||
    'unknown'
  );
};

//   
export const setSecurityHeaders = (response: any) => {
  response.setHeader('X-Content-Type-Options', 'nosniff');
  response.setHeader('X-Frame-Options', 'DENY');
  response.setHeader('X-XSS-Protection', '1; mode=block');
  response.setHeader('Referrer-Policy', 'strict-origin-when-cross-origin');
  response.setHeader('Permissions-Policy', 'geolocation=(), microphone=(), camera=()');
};

//  
export const validateEnvironmentVariables = (): { isValid: boolean; missing: string[] } => {
  const requiredVars = [
    'VITE_FIREBASE_API_KEY',
    'VITE_FIREBASE_AUTH_DOMAIN',
    'VITE_FIREBASE_DATABASE_URL',
    'VITE_FIREBASE_PROJECT_ID'
  ];

  const missing: string[] = [];
  
  for (const varName of requiredVars) {
    if (!import.meta.env[varName]) {
      missing.push(varName);
    }
  }

  return {
    isValid: missing.length === 0,
    missing
  };
};

//   ( Base64 )
export const encryptData = (data: string): string => {
  return btoa(encodeURIComponent(data));
};

//  
export const decryptData = (encryptedData: string): string => {
  try {
    return decodeURIComponent(atob(encryptedData));
  } catch (error) {
    throw new Error('  ');
  }
};

//   
export const createSessionTimeout = (timeoutMs: number = 30 * 60 * 1000) => {
  let timeoutId: NodeJS.Timeout;
  
  const resetTimeout = () => {
    clearTimeout(timeoutId);
    timeoutId = setTimeout(() => {
      //    
      if (typeof window !== 'undefined') {
        window.location.href = '/login';
      }
    }, timeoutMs);
  };

  const clearSessionTimeout = () => {
    if (timeoutId) {
      clearTimeout(timeoutId);
    }
  };

  return { resetTimeout, clearTimeout: clearSessionTimeout };
};
