// IATA/ICAO    (IATA -> ICAO )
const getICAOCode = (airlineCode: string): string => {
  const iataToIcaoMap: { [key: string]: string } = {
    'OZ': 'AAR',  // Asiana Airlines
    'KE': 'KAL',  // Korean Air
    '7C': 'JJA',  // Jeju Air
    'TW': 'TWB',  // T'way Air
    'BX': 'ABL',  // Air Busan
    'ZE': 'ESR',  // Eastar Jet
    'LJ': 'JNA',  // Jin Air
    'RS': 'ASV',  // Air Seoul
    'YP': 'APZ',  // Air Premia
    'RF': 'EOK',  // Aerokorea
    'NH': 'ANA',  // All Nippon Airways
    'JL': 'JAL',  // Japan Airlines
    'MM': 'APJ',  // Peach Aviation
  };
  
  //  ICAO   (3)
  if (airlineCode.length === 3) {
    // ICAO    
    return airlineCode;
  }
  
  // IATA   ICAO 
  return iataToIcaoMap[airlineCode] || airlineCode;
};

//   
const getAirlineName = (iataCode: string): string => {
  const airline = worldAirlines.find(a => a.iata === iataCode);
  return airline?.koreanName || iataCode;
};

//     (   204  )
const checkNetworkStatus = async (): Promise<boolean> => {
  try {
    // navigator.onLine    
    if (!navigator.onLine) {
      return false;
    }

    //     (204 ,  , SW )
    await fetch('https://www.google.com/generate_204', {
      method: 'GET',
      mode: 'no-cors',
      cache: 'no-cache',
      signal: AbortSignal.timeout(3000)
    });
    // no-cors  opaque     
    return true;
  } catch (error) {
    return false;
  }
};

//   (      )
const safeReload = async (reason: string = '') => {
  console.log(` ${reason} `);
  //     
  if (!navigator.onLine) {
    console.log('  :  ');
    return false;
  }
  try {
    //         
    const latest = await isLatestVersion();
    if (!latest) {
      await checkAndUpdate();
      return true;
    }
    //     (  )
    console.log('   .  .');
    return false;
  } catch (e) {
    console.warn('    .  :', e);
    return false;
  }
};


